<template>
  <div class="main">
    <auto-header>
      <template v-slot:left>
        <div class="name hidden-sm-and-down">
          <back-button :link="`/workspace/${workspaceId}`"></back-button>
          <div>New proposal</div>
        </div>
        <back-button class="hidden-md-and-up"></back-button>
      </template>
      <h1>{{ name }}</h1>
      <template v-slot:right
        ><rect-button
          @click="publish"
          width="150px"
          :disabled="fullscreenLoading"
        >
          Publish
        </rect-button></template
      >
    </auto-header>
    <div class="detail" v-loading="fullscreenLoading">
      <div class="title">Proposal settings</div>
      <el-row :gutter="40" class="settings">
        <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8">
          <div class="item">
            <el-date-picker
              v-model="startTime"
              type="datetime"
              placeholder="Select start date"
              align="left"
              value-format="timestamp"
              prefix-icon="el-icon-date"
              @change="timeChange"
            >
            </el-date-picker></div
        ></el-col>
        <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8">
          <div class="item">
            <el-date-picker
              v-model="endTime"
              type="datetime"
              placeholder="Select end date"
              value-format="timestamp"
              align="left"
              prefix-icon="el-icon-date"
              @change="timeChange"
            >
            </el-date-picker>
          </div>
        </el-col>
        <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8">
          <el-input
            class="item"
            type="Number"
            prefix-icon="el-icon-s-grid"
            placeholder="Block height"
            v-model="height"
          ></el-input>
        </el-col>
      </el-row>
      <div class="title">Proposal options</div>
      <el-row :gutter="40" class="settings">
        <el-col
          :xs="24"
          :sm="12"
          :md="8"
          :lg="8"
          :xl="8"
          v-for="(item, index) in info.options"
          :key="index"
        >
          <div class="item">
            <el-input v-model="item.label"></el-input>
            <img
              class="close_image"
              src="../assets/img/close.png"
              alt=""
              @click="removeChoice(index)"
            />
          </div>
        </el-col>
        <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8">
          <rect-button width="100%" @click="addChoice">Add options</rect-button>
        </el-col>
      </el-row>

      <div class="question">
        <el-input v-model="info.title" placeholder="Proposal Title"></el-input>
      </div>
      <div class="content">
        <vue-easymde
          ref="markdownEditor"
          :configs="configs"
          v-model="info.content"
        />
      </div>
    </div>
  </div>
</template>

<script>
import {
  addProposal,
  getBlockNumber,
  checkMetaMask
} from "../matalab/witness.js";
import Network from "../matalab/global.js";
import VueEasymde from "vue-easymde";
import AutoHeader from "../components/AutoHeader.vue";
import RectButton from "../components/RectButton.vue";
import BackButton from "../components/BackButton.vue";

export default {
  data() {
    return {
      startTime: "",
      endTime: "",
      height: "",
      fullscreenLoading: false,
      itemList: [],
      info: {
        title: "",
        content: "Enter yout proposal content here",
        resultPublish: 0,
        options: [
          {
            index: 0,
            label: "Approve"
          },
          {
            index: 1,
            label: "Reject"
          }
        ]
      },
      name: "",
      workspaceId: "",
      configs: {
        status: false,
        spellChecker: false
      }
    };
  },
  components: {
    VueEasymde,
    AutoHeader,
    RectButton,
    BackButton
  },
  mounted() {
    this.name = this.$route.params.name;
    this.workspaceId = this.$route.params.id;
  },
  computed: {
    easymde() {
      return this.$refs.markdownEditor.easymde;
    }
  },
  methods: {
    addChoice() {
      var obj = {
        index:
          this.info.options.length == 0
            ? 0
            : this.info.options[this.info.options.length - 1].index + 1,
        //label:this.info.options.length==0?0:this.info.options[this.info.options.length-1].index+1
        label: "Option"
      };
      this.info.options.push(obj);
    },
    removeChoice(item) {
      this.info.options.splice(item, 1);
    },
    timeChange() {
      if (this.startTime > this.endTime && this.endTime != "") {
        this.$notify.error({
          title: "Error",
          message: "The end date cannot be earlier than the start date!",
          duration: 0
        });
        this.endTime = "";
      }
      let height = getBlockNumber();
      height.then(res => {
        this.height = res;
      });
    },
    selectResultPublish(index) {
      this.info.resultPublish = index;
    },
    validateNotify(message) {
      this.$notify.error({ title: "Error", message: message, offset: 100 });
    },
    validateInfo() {
      if (this.info.title === "") {
        this.validateNotify("Proposal title can not be epmty");
        return false;
      } else if (this.startTime === "" || this.endTime === "") {
        this.validateNotify("Date not choosed");
        return false;
      } else if (this.height > 0) {
        if (this.info.options.length > 1) {
          for (let option of this.info.options) {
            if (option.label === "") {
              this.validateNotify("The options can not be epmty");
              return false;
            }
            return true;
          }
        } else {
          this.validateNotify("The proposal need at least two options ");
          return false;
        }
      } else {
        this.validateNotify("Block height is invalid");
        return false;
      }
    },
    async publish() {
      const check = await this.checkNetwork();
      if (check) {
        const valid = this.validateInfo();
        if (valid) {
          this.fullscreenLoading = true;
          let id = this.$route.params.id;
          let proposalInfo = {
            title: "",
            content: "",
            options: []
          };
          proposalInfo.title = this.info.title;
          proposalInfo.content = this.easymde.markdown(this.info.content);
          for (let index in this.info.options) {
            proposalInfo.options.push(
              this.info.options[index].label.toString()
            );
          }
          let result = addProposal(
            id,
            this.startTime / 1000,
            this.endTime / 1000,
            this.height,
            proposalInfo
          );
          result
            .then(res => {
              console.debug(res);
              this.fullscreenLoading = false;
              this.$notify({
                title: "Success",
                message: "Proposal create successfully",
                offset: 100,
                type: "success"
              });
              this.$router.replace({
                name: "workspace",
                params: { id: this.$route.params.id }
              });
            })
            .catch(err => {
              console.debug(err);
              this.validateNotify("Publish failed");
              this.fullscreenLoading = false;
            });
        }
      }
    },

    async checkNetwork() {
      const installed = await checkMetaMask();
      if (installed) {
        let chainId = await window.ethereum.request({ method: "eth_chainId" });
        if (parseInt(chainId) === Network.automataChainId) {
          return true;
        } else {
          this.validateNotify(
            "You need to connect to Automata network to publish."
          );
          return false;
        }
      } else {
        this.validateNotify(
          "Looks like MetaMask extension is missing on your browser."
        );
        return false;
      }
    }
  }
};
</script>

